import Nickname from "@/components/actionbar/Nickname.vue"
export default{
	data(){
		return {
			// CustomBar: this.CustomBar,
			bool:true,
			text:"保存",
			modalName: null,
			value:"",
			imgList: [],
			pathurl:"",
			show_img_list:[],
			json:"",
		}
	},
	components:{
		Nickname,
	},
	methods:{
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Modify(){
			this.value = this.value
			this.modalName = null
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			this.imgList.splice(e.currentTarget.dataset.index, 1)
		},
		ChooseImage() {
			const _self = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					this.imgList = res.tempFiles
					this.pathurl = res.tempFilePaths[0]
				}
			});
		},
		//点击上传
		Confirmupload(){
			this.bool = false
			this.text = "确认修改"
			console.log()
			//设置缓存 将这个图片存到缓存里
			uni.setStorage({
				key:"dandutouxiang",
				data:this.pathurl
			})
			// if(this.imgList[0].size>5120){
			// 	this.modalName = null
			// 	uni.showToast({
			// 		title:"图片的大小不许超过5M",
			// 		icon:"none",
			// 		duration:2000
			// 	})
			// 	return false
			// }else{
				const _this = this
				uni.uploadFile({
					url:"http://hbk.huiboke.com/api/common/uploadImage?type=user",
					filePath:this.pathurl,
					name:"file",
					fileType:"image",
					success(res){
						
						this.json = JSON.parse(res.data)
						// console.log(this.json,"333")
						this.pathurl =this.json.data.src
						_this.jsons(this.json)
						// console.log(_this.jsons)//这能走过来
					}
				})
			// }
			//这是将弹窗关闭
			this.modalName = null
		},
		//改变子组件传过来的值
		changebool(e){
			this.bool = e
		},
		changetext(e){
			this.text = e
		},
		jsons(e,jsonsrc){
			// console.log(e)
			// e = jsonsrc
			this.json = e
			console.log(this.json)
		}
	},
	// created() {
	// 	const _this = this
	// 	uni.getStorage({
	// 		key:"dandutouxiang",
	// 		success(res){
	// 			_this.pathurl = res.data
	// 		}
	// 	})
	// }
}
